.footer {
  position: fixed;
  bottom: 0;
  z-index: 0;
  display: flex;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
  color: var(--text-white);
  background-color: var(--text-color);

  &__nav {
    display: grid;
    grid-template-areas:
      "logo navigate"
      "...  contacts";
    grid-template-columns: 150px 1fr;
    overflow: hidden;

    @include tablet {
      grid-template-areas:
        "logo    "
        "navigate"
        "contacts";
      grid-template-columns: minmax(150px, 1fr);
    }
  }

  &__logo-link {
    grid-area: logo;
    border-top: 1px solid var(--footer-border);
    border-bottom: 1px solid var(--footer-border);
    padding: 21px 40px;

    path {
      fill: var(--text-white);
      transition: fill 0.3s ease-in-out;
    }

    &:hover path {
      fill: var(--main-color);
      transition: fill 0.3s ease-in-out;
    }

    @include tablet {
      text-align: center;
    }
  }

  &__logo-svg path {
    fill: var(--text-white);
  }

  &__navigate {
    display: flex;
    flex-direction: column;
    grid-area: navigate;
    border-left: 1px solid var(--footer-border);
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    grid-area: contacts;
    border-left: 1px solid var(--footer-border);
    padding-top: 44px calc(100% - var(--container-offset));
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    line-height: 140%;
    letter-spacing: -0.4px;

    @include tablet {
      border-left: none;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--footer-border);
    padding: 44px 20px 56px;

    @include tablet {
      flex-direction: column;
      align-items: center;
      padding: 24px 20px 56px;
    }
  }

  &__info {
    position: relative;
    display: flex;
    flex-direction: column;
  }

  &__copyright {
    display: flex;
    align-self: flex-end;
    justify-content: space-between;
    padding: 20px 24px;
    width: 100%;

    @include tablet {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  @include tablet {
    position: relative;
  }

  @include small-tablet {
    height: auto;
  }
}

.contacts {
  font-family: "PTRoot", sans-serif;
  font-weight: 500;
  font-style: normal;
  line-height: 140%;
  letter-spacing: -0.4px;

  &__title {
    margin: 0;
    font-weight: 500;
    font-style: normal;
    font-size: 12px;
    color: var(--text-grey-mini);

    &_gap {
      padding-bottom: 5px;
    }

    @include tablet {
      text-align: center;
    }
  }

  &__address {
    @include tablet {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  &__text {
    margin: 0;
    padding-bottom: 8px;
    max-width: 266px;
    font-size: 20px;
    color: var(--text-white);

    @include tablet {
      text-align: center;
    }
  }

  &__link {
    color: var(--text-white);

    &_under {
      font-weight: 500;
      font-style: normal;
      font-size: 12px;
      text-decoration: underline;
      color: var(--text-grey-mini);

      @include tablet {
        text-align: center;
      }
    }

    @include mobile {
      font-size: 20px;
    }
  }
}

.site-cedro {
  font-family: "PTRoot", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 12px;
  letter-spacing: -0.24px;
  text-align: center;
  color: var(--text-grey-mini);

  &__name {
    text-transform: uppercase;
  }
}

.copyright {
  &__years {
    font-family: "PTRoot", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 12px;
    letter-spacing: -0.24px;
    color: var(--text-grey-mini);
  }

  &__link {
    font-family: "PTRoot", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 12px;
    letter-spacing: -0.24px;
    text-decoration: underline;
    color: var(--text-grey-mini);
  }

  @include tablet {
    padding: 0 190px;
    text-align: center;
  }

  @include small-tablet {
    padding: 0 100px;
    text-align: center;
  }

  @include mobile {
    padding: 0 60px;
    text-align: center;
  }
}

.social {
  &__list {
    display: flex;
  }

  &__item {
    position: relative;
    margin-right: 12px;
    width: 32px;
  }

  &__link {
    font-size: 0;
    color: var(--text-white);

    &_tg {
      content: "";
      position: absolute;
      right: 0;
      width: 32px;
      height: 32px;
    }

    &_vk {
      content: "";
      position: absolute;
      left: 0;
      width: 32px;
      height: 32px;
    }
  }

  &__svg_tg {
    circle {
      fill: var(--footer-icon-fill);
      transition: fill 0.3s ease-in-out;
    }

    path {
      fill: #4d4d4d;
      transition: fill 0.3s ease-in-out;
    }

    &:hover {
      circle {
        fill: var(--footer-icon-fill);
        transform: fill 0.3s ease-in-out;
      }

      path {
        fill: var(--main-color);
        transition: fill 0.3s ease-in-out;
      }
    }
  }
}

.ticker {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1 1 auto;
  overflow: hidden;
  padding-top: var(--padding-footer);
  padding-bottom: var(--padding-footer);

  @include tablet {
    padding-top: 64px;
    padding-bottom: 64px;
  }

  &__string {
    display: flex;
    flex-shrink: 0;
    padding-bottom: 5px;
    width: 320vw;
    font-weight: 500;
    font-size: 72px;
    // font-size: 4em;
    line-height: 72px;
    letter-spacing: 1.44px;
    white-space: nowrap;
    text-align: left;
    text-transform: uppercase;
    animation-name: ticker;
    animation-duration: 50s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;

    &_stroke {
      position: relative;
      z-index: inherit;
      border: none;
      padding: 0 48px;
      font-size: 73px;
      -webkit-text-stroke: 1px var(--text-white);
      color: var(--text-color);
      background: transparent;

      &::after {
        content: "";
        position: absolute;
        left: 100%;
        top: 50%;
        width: 14px;
        height: 14px;
        background-image: url("../img/svg/dot.svg");
        transform: translate(-50%, -50%);
      }

      @include tablet {
        font-size: 55px;
      }
    }

    &_fill {
      position: relative;
      padding: 0 48px;
      color: var(--text-white);
      z-index: inherit;
      border: none;
      background: transparent;

      &::after {
        content: "";
        position: absolute;
        left: 100%;
        top: 50%;
        width: 14px;
        height: 14px;
        background-image: url("../img/svg/dot.svg");
        transform: translate(-50%, -50%);
      }
    }

    &:hover {
      animation-play-state: paused;
    }

    &:nth-child(even) {
      animation-direction: reverse;
    }

    @include tablet {
      font-size: 54px;
    }
  }
}

@keyframes ticker {
  0% {
    transform: translateZ(0);
  }

  100% {
    transform: translate3d(-25%, 0, 0);
  }
}