.admin {
  position: relative;
  z-index: 11;
  z-index: 14;
  background-color: var(--secondary-white);

  &__container {
    position: relative;
    // left: 50%;
    top: -132px;
    // transform: translateX(-50%);
    padding: 0 var(--container-offset);
    max-width: var(--content-width);
    background-color: var(--text-white);

    &::before {
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      width: 988px;
      height: 620px;
      background-image: url("../img/map-cites.png");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;

      @include big-desktop {
        top: 100px;
        width: 684px;
        height: 500px;
      }

      @include tablet {
        top: 70px;
        width: 377px;
        height: 430px;

        @include mobile {
          top: 65px;
          width: 310px;
          height: 310px;
        }
      }

      @include small-mobile {
        top: 55px;
        width: 310px;
        height: 310px;
      }
    }

    @include tablet {
      max-width: 740px;
    }

    @include small-tablet {
      padding: 0 var(--container-offset-low);
    }

    @include small-mobile {
      position: absolute;
      left: 50%;
      top: -68px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      overflow: visible;
      padding: 0;
      max-width: 343px;
      transform: translateX(-50%);
    }
  }

  &__title {
    margin: 0;
    padding: 28px 0 235px;
    max-width: 395px;

    @include tablet {
      padding-bottom: 395px;
    }

    @include small-mobile {
      padding: 16px;
      font-weight: 500;
      font-size: 40px;
      white-space: nowrap;
      text-align: center;
    }
  }

  &__lands {
    display: flex;
    padding-bottom: 40px;

    @include small-mobile {
      padding-top: 250px;
      padding-left: 16px;
      flex-direction: column;
      padding-bottom: 24px;
    }
  }
}

.lands {
  &:not(:last-child) {
    padding-right: 30px;
  }

  &__value {
    margin: 0;
    font-size: 48px;
    line-height: 110%;
    letter-spacing: 1.44px;
    text-transform: uppercase;
    color: var(--text-color);

    @include small-mobile {
      font-size: 28px;
    }
  }

  &__word {
    margin-top: 2px;
    font-family: "PTRoot", sans-serif;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: -0.32px;
    text-transform: lowercase;
    color: var(--text-grey-secondary);

    @include small-mobile {
      padding-right: 150px;
      font-size: 12px;
    }
  }
}