.main-gallery {
  z-index: 10;
  background-color: var(--text-color);

  &__title {
    margin-left: 33%;
    text-align: left;
    color: var(--text-white);

    @include tablet {
      margin-left: 2%;
      font-size: 54px;
    }

    @include mobile {
      font-size: 40px;
    }
  }
}

.panel {
  height: 100vh;
}

.gallery-scroll {
  overflow: hidden;

  &__control,
  &__experts,
  &__charges {
    overflow: hidden;
    min-height: 100vh;
    background-color: var(--text-color);
  }

  &__count {
    flex-grow: 1;
    margin: 0;
    font-family: "DrukCyr", sans-serif;
    font-size: 48px;
    line-height: 82%;
    letter-spacing: 1.44px;
    text-transform: uppercase;
    color: var(--numb-grey);
  }

  &__title {
    flex-shrink: 0;
    margin: 0;

    @include tablet {
      margin-bottom: 15px;
    }
  }

  &__img {
    width: 100%;
    height: 100%;

    @include mobile {
      max-height: 518px;
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    grid-column: 4 span;

    &_top {
      display: flex;
      padding: 20px;

      @include tablet {
        padding: 15px;
      }
    }

    &_bottom {
      border-top: 1px solid var(--footer-border);
      padding: 40px;

      @include tablet {
        padding: 15px;
      }
    }

    @include tablet {
      grid-column: 12 span;
      grid-row: 1 / 2;
    }
  }

  &__image {
    grid-column: 8 span;
    border-left: 1px solid var(--footer-border);
    width: 100%;
    height: 100%;

    // background-image: url('../../img/charges.jpg');
    // background-position: -400px top;
    // background-repeat: no-repeat;
    // background-size: cover;

    @include tablet {
      grid-column: 12 span;
      grid-row: 2 / 3;
    }

    @include small-tablet {
      grid-column: 3 / 8 span;
    }

    @include mobile {
      grid-column: 12 span;
    }
  }

  &__text {
    flex-shrink: 0;
    margin: 0;
    font-family: "PTRoot", sans-serif;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: -0.32px;
    color: var(--text-white);

    @include tablet {
      font-size: 14px;
      line-height: 130%;
    }

    @include mobile {
      padding-right: 30px;
    }
  }
}

.wrap-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  border-top: 1px solid var(--footer-border);
  border-bottom: 1px solid var(--footer-border);
  min-height: 100vh;

  @include tablet {
    grid-template-rows: minmax(180px, 240px) 1fr;
  }
}