.expert {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;

  &__picture {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    @include small-tablet {
      height: 100%;
    }
  }

  &__position {
    display: inline-block;
    margin: 0;
    margin-bottom: 16px;
    border: 1px solid rgba(255, 255, 255, 0.32);
    border-radius: 59px;
    padding: 4px 6px;
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 12px;
    line-height: 100%;
    letter-spacing: -0.24px;
    color: var(--light-color);
    background-color: rgba(255, 255, 255, 0.24);
  }

  &__name {
    margin: 0;
    font-family: "PTRoot", sans-serif;
    font-weight: 700;
    font-size: 32px;
    line-height: 100%;
    color: var(--light-color);
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      padding-right: 20px;

      &::after {
        content: "";
        position: absolute;
        right: 4%;
        bottom: 17%;
        width: 5px;
        height: 5px;
        background-image: url("../img/svg/dot-mini.svg");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        transform: translate(-50%, -50%);
      }
    }
  }

  &__contact {
    display: flex;
    color: var(--light-color);
  }

  &__about {
    position: relative;
    padding: 24px;
  }

  &__experience {
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    color: var(--text-grey-card);
  }

  &__number,
  &__mail {
    font-family: "PTRoot", sans-serif;
    font-size: 14px;
    line-height: 140%;
    text-transform: lowercase;
    color: var(--light-color);

    @include tablet {
      font-size: 11px;
    }
  }
}