.nav {
  position: relative;

  &__list {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;

    @include tablet {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      height: 100vh;
    }
  }

  &__item {
    padding: 20px 32px 20px 20px;
  }

  //  

  &__btn {
    margin: 0;
    padding: 20px 32px 20px 20px;
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 18px;
    line-height: 90%;
    letter-spacing: -0.36px;
    white-space: nowrap;
    text-transform: lowercase;
    color: var(--text-color);
    transition: color 0.3s ease-in-out;
    cursor: pointer;

    &:hover {
      color: var(--main-color);
      transition: color 0.3s ease-in-out;
    }

    &.accordion__control {
      position: relative;

      &::before {
        visibility: hidden;
        transition: visibility 0.3s ease-in-out;
      }

      &::after {
        content: "";
        position: absolute;
        right: 2px;
        top: 20px;
        width: 18px;
        height: 18px;
        background-image: url("../../img/svg/plus-svg.svg");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        visibility: visible;
        transition: visibility 0.3s ease-in-out;

        @include tablet {
          background-image: url("../../img/svg/plus-footer-svg.svg");
        }
      }
    }

    &_active {
      color: var(--main-color);
      transition: color 0.3s ease-in-out;

      &.accordion__control {
        &::before {
          content: "";
          position: absolute;
          right: 2px;
          top: 20px;
          width: 18px;
          height: 18px;
          background-image: url("../../img/svg/minus-svg.svg");
          background-position: center;
          background-size: cover;
          background-repeat: no-repeat;
          visibility: visible;
          transform: rotate(180deg);
          transition: visibility 0.3s ease-in-out;
          animation: 0.5s rotate-plus;

          @include tablet {
            background-image: url("../../img/svg/minus-footer-svg.svg");
          }
        }

        &::after {
          visibility: hidden;
          transition: visibility 0.3s ease-in-out;
        }
      }
    }

    @include tablet {
      display: inline;
      // width: 100%;
      text-align: left;
      color: var(--text-white);
      transition: color 0.3s ease-in-out;
    }
  }

  //  

  &__item {
    margin: 0;
    padding: 0;
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 18px;
    line-height: 90%;
    letter-spacing: -0.36px;
    white-space: nowrap;
    text-transform: lowercase;
    color: var(--text-color);
    transition: color 0.3s ease-in-out;
    cursor: pointer;

    &_btn {
      border: none;
      border-left: 1px solid var(--header-border);
      width: 100%;
      opacity: 0;
      visibility: hidden;

      @include tablet {
        opacity: 1;
        visibility: visible;
      }
    }

    &_active {
      color: var(--main-color);
      transition: color 0.3s ease-in-out;
    }

    @include tablet {
      width: 100%;

      &:last-child {
        align-self: flex-end;
      }
    }
  }

  .footer &__link {
    padding: 0;
    color: var(--text-white);
    transition: color 0.3s ease-in-out;

    &.accordion__control {
      position: relative;

      &::before {
        visibility: hidden;
        transition: visibility 0.3s ease-in-out;
      }

      &::after {
        content: "";
        position: absolute;
        right: -28px;
        top: 2px;
        width: 18px;
        height: 18px;
        background-image: url("../../img/svg/plus-footer-svg.svg");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        visibility: visible;
        transition: visibility 0.3s ease-in-out;
      }

      &.nav__btn_active {
        transition: color 0.3s ease-in-out;

        &.accordion__control {
          &::before {
            content: "";
            position: absolute;
            right: -28px;
            top: 2px;
            width: 18px;
            height: 18px;
            background-image: url("../../img/svg/minus-footer-svg.svg");
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            visibility: visible;
            transform: rotate(180deg);
            transition: visibility 0.3s ease-in-out;
            animation: 0.5s rotate-plus;
          }

          &::after {
            visibility: hidden;
            transition: visibility 0.3s ease-in-out;
          }
        }
      }
    }

    &:hover {
      color: var(--main-color);
      transition: color 0.3s ease-in-out;
    }

    @include tablet {
      text-align: center;
    }
  }

  .footer &__list {
    @include tablet {
      height: 100%;
    }
  }

  .footer &__item {
    padding: 22px 32px 22px 20px;

    @include tablet {
      padding: 22px 0;
      text-align: center;
    }
  }
}

.header .nav {
  margin-right: 0;
}

.footer .nav {
  margin-right: -1px;
  border-top: 1px solid var(--footer-border);
  border-bottom: 1px solid var(--footer-border);

  @include tablet {
    border: none;
    border-bottom: 1px solid var(--footer-border);
    margin-right: 0;
  }
}

.menu--active {
  visibility: visible;
  transform: translateY(0);
  transition: transform 0.5s ease-in-out;

  &.nav {
    transition: transform 0.5s ease-in-out;

    @include tablet {
      height: 100vh;
      justify-content: space-between;
    }
  }
}

@keyframes rotate-plus {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(180deg);
  }
}