.news {
  z-index: 10;
  margin-bottom: 100vh;
  padding-top: var(--block-bottom-desk);
  padding-bottom: var(--block-bottom-desk);
  background-color: var(--text-white);

  &__text {
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-size: 20px;
  }

  &__title {
    margin: 0;

    @include tablet {
      padding-left: 8px;
    }

    @include small-tablet {
      padding-bottom: 24px;
    }
  }

  &__link {
    align-self: flex-start;

    @include tablet {
      position: relative;
      left: 7px;
      margin-top: 14px !important;
    }

    @include mobile {
      left: 1px;
    }
  }

  &__nav {
    @include flex-v-center;

    visibility: hidden;
    position: relative;
    left: -15px;
    top: -10px;
    justify-content: space-between;
    min-width: 90px;

    @include tablet {
      visibility: visible;
    }
  }

  &__slider {
    order: 1;

    @include tablet {
      order: 0;
    }
  }

  &__btn {
    position: relative;
    border: 1px solid var(--header-border);
    border-radius: 100%;
    width: 40px;
    height: 40px;
    color: var(--text-color);
    -webkit-transition: stroke 0.3s ease-in-out;
    transition: border 0.3s ease-in-out;

    svg {
      position: absolute;
      left: 50%;
      top: 50%;
      width: 18px;
      height: 18px;
      color: inherit;
      stroke: currentColor;
      object-fit: cover;
      transform: translate(-50%, -50%);
      transition: stroke 0.3s ease-in-out;
    }

    &:hover {
      border: 1px solid var(--text-color);
      transition: border 0.3s ease-in-out;
    }
  }

  &__block {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__img {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    object-fit: cover;

    @include tablet {
      width: 100%;
      min-height: 536px;
      height: auto;
    }

    @include small-tablet {
      width: 100%;
      height: 100%;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    background-color: #fff;

    &_flex {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      border: 1px solid var(--header-border);

      @include tablet {
        border: 1px solid var(--header-border);
      }
    }
  }

  &__slide {
    &__news-text {
      position: relative;
      z-index: 1;
      padding: 364px 24px 24px;

      @include small-tablet {
        padding: 364px 15px 24px;
      }
    }
  }

  &__card {
    padding: 62px 24px 24px;
    padding: 62px 24px 54px;

    &:not(:last-child) {
      border-bottom: 1px solid var(--header-border);
    }
  }

  @include tablet {
    margin-bottom: 0;
    padding-top: var(--block-top-tablet);
  }

  @include small-tablet {
    padding-top: var(--block-top-tablet);
  }
}

@media (max-width: 1024px) {
  .news__slider .swiper-wrapper {
    display: flex;
    column-gap: 0;
  }
}

.news-info {
  position: relative;
  background-size: 100% 0;
  -webkit-transition: background-size 0.3s ease-in-out, color 0.5s ease-in-out;
  transition: background-size 0.3s ease-in-out, color 0.5s ease-in-out;
  cursor: pointer;

  &__date {
    display: inline-block;
    border: 1px solid var(--header-border);
    border-radius: 59px;
    padding: 3px 4px;
    font-family: "PTRoot", sans-serif;
    font-size: 12px;
    line-height: 100%;
    letter-spacing: -0.24px;
    color: #000;
    background-color: transparent;

    &_slide {
      border: 1px solid rgba(255, 255, 255, 0.32);
      color: var(--text-white);
      background-color: rgba(255, 255, 255, 0.24);
    }
  }

  &__title {
    margin: 0;
    padding-bottom: 12px;
    font-family: "PTRoot", sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 32px;
    line-height: 100%;
    color: #000;
    background-color: transparent;

    &_slide {
      color: var(--text-white);
    }

    @include small-tablet {
      white-space: nowrap;
    }

    @include mobile {
      white-space: wrap;
    }
  }

  &__text {
    margin: 0;
    font-family: "PTRoot", sans-serif;
    font-size: 14px;
    line-height: 140%;

    &_slide {
      color: var(--text-white);
    }
  }

  &:hover {
    color: var(--text-white);
    background: linear-gradient(#8a8cce, #8a8cce);
    background-position: 0% 100%;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    -webkit-transition: background-size 0.3s ease-in-out, color 0.5s ease-in-out;
    transition: background-size 0.3s ease-in-out, color 0.5s ease-in-out;

    .news-info__title,
    .news-info__date {
      color: var(--text-white);
    }
  }
}

.swiper {
  overflow: hidden;

  @include tablet {
    overflow: hidden;
  }
}