.dropdown {
  position: absolute;
  left: 0;
  top: 63px;
  z-index: 15;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  background-color: var(--text-white);
  visibility: hidden;
  transform: translateY(-500px);
  transition: transform 0.5s ease-in-out, visibility 0.3s ease-in-out;
  cursor: pointer;

  &_active {
    visibility: visible;
    transform: translateY(4px);
    transition: transform 0.5s ease-in-out, visibility 0.2s ease-in-out;
  }

  &__item {
    padding: 20px 32px 20px 20px;
    width: 100%;
    background-size: 100% 0;
    transition: background-size 0.3s ease-in-out, color 0.5s ease-in-out;

    &:hover {
      background: linear-gradient(#8a8cce, #8a8cce);
      background-position: 0% 100%;
      background-size: 100% 100%;
      background-repeat: no-repeat;
      transition: background-size 0.3s ease-in-out, color 0.5s ease-in-out;

      .dropdown__link {
        color: var(--text-white);
        transition: color 0.3s ease-in-out;
      }
    }

    @include tablet {
      padding: 20px 132px 20px 20px;
      background-color: var(--text-color);
    }

    @include mobile {
      padding: 20px 70px 16px 20px;
    }
  }

  &__list {
    background: var(--text-white);

    @include tablet {
      border-top: 1px solid var(--footer-border);
      border-bottom: 1px solid var(--footer-border);
      background: var(--text-color);
    }
  }

  &__link {
    margin: 0;
    padding: 0;
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 18px;
    line-height: 90%;
    letter-spacing: -0.36px;
    white-space: nowrap;
    text-transform: lowercase;
    color: var(--text-color);
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
    cursor: pointer;

    @include tablet {
      white-space: wrap;
      color: var(--text-white);
    }
  }

  @include tablet {
    position: relative;
    left: 0;
    top: 0;
    visibility: visible;
    transform: none;
  }
}

