.main-input {
  border-bottom: 1px solid #4d4d4d;
  font-family: "DrukCyr", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 48px;
  line-height: 110%;
  letter-spacing: 1.44px;
  text-transform: uppercase;

  &.error {
    border-bottom: 2px solid var(--text-tomato);
    color: var(--text-tomato);
  }
}
