.mouse {
  position: fixed;
  left: 1px;
  top: 0;
  z-index: 1000;
  border-radius: 100%;
  width: 0;
  height: 0;
  box-shadow: 0 8px 24px 0 rgba(23, 23, 23, 0.32), 0 4px 4px 0 rgba(23, 23, 23, 0.20);
  background: rgba(138, 140, 206, 0.64);
  transition: all 0.05s ease-in-out;
  will-change: max-height;
  pointer-events: none;
  backdrop-filter: blur(12px);

  &__active {
    width: 140px;
    height: 140px;
    transition: all 0.05s ease-in-out;
  }

  &__active .mouse-view {
    font-size: 18px;
  }
}

.mouse-view {
  padding: 43px 30px;
  font-family: "PTRoot", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 0;
  line-height: 90%;
  letter-spacing: -0.36px;
  text-align: center;
  text-transform: lowercase;
  color: var(--text-white);
  transform: rotate(-15deg);
}