@include tablet {
  .accordion {
    &__control {
      overflow: hidden;
      overflow: visible;
      cursor: pointer;
    }

    &__content {
      max-height: 0;
      opacity: 0;
      visibility: hidden;
      transform: translateY(-300px);
      transition: all 0.5s ease-in-out;
      will-change: max-height;
    }

    .open &__content {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
      transition: all 0.5s ease-in-out;
    }
  }
}

.footer__item .accordion__control {
  // overflow: hidden;
  cursor: pointer;
}

.footer__item .accordion__content {
  max-height: 0;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-1300px);
  transition: all 0.5s ease-in-out;
  will-change: max-height;
}

.footer__item.open {
  .accordion__content {
    opacity: 1;
    visibility: visible;
    transform: translateY(-1px);
    transition: all 0.5s ease-in-out;

    @include tablet {
      transform: translateY(22px);
    }
  }
}