:root {
  // base
  --font-family: "DrukCyr", sans-serif;
  --content-width: 1200px;
  --content-width-middle: 1400px;
  --container-offset: 40px;
  --container-offset-low: 20px;
  --container-offset-full-hd: 240px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --block-bottom-desk: 96px;
  --block-top-desk: 140px;
  --block-top-tablet: 60px;
  --block-top-mobile: 20px;
  --padding-footer: 84px;

  // colors
  --light-color: #fff;
  --main-color: #8a8cce;
  --main-color-active: #6d70b8;
  --text-color: #2e2e2e;
  --numb-grey: #707070;
  --text-white: #fff;
  --secondary-white: #fdfdfd;
  --text-grey-secondary: #adadad;
  --text-grey-mini: #707070;
  --text-grey-card: #a8a9a9;
  --error-tomato: #ee6a5f;
  --footer-border: #4c4c4c;
  --header-border: #e4e4e4;
  --footer-icon-fill: #fff;
}

// main-shadow
$border-shadow: 100px;
$background: rgba(138, 140, 206, 64%);
$box-shadow: 0 8px 24px 0 rgba(23, 23, 23, 32%);
$backdrop-filter: blur(12px);