.error-block {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 15;
  display: flex;
  justify-content: center;
  width: 100%;
  color: var(--text-white);
  background-color: var(--error-tomato);
  visibility: hidden;
  transform: translateY(-800px);
  transition: transform 0.8s ease-in-out, visibility 0.8s ease-in-out;

  &__text {
    padding: 0 18px;
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: -0.4px;

    @include mobile {
      font-size: 16px;
    }
  }

  &_active {
    visibility: visible;
    transform: translateY(-4px);
    transition: transform 0.5s ease-in-out, visibility 0.5s ease-in-out;
  }
}