.slider-experts {
  position: relative;

  &__nav {
    @include flex-v-center;

    left: 305px;
    top: 40px;
    justify-content: space-between;
    min-width: 90px;
    visibility: visible;

    @include small-tablet {
      visibility: hidden;
    }
  }

  &__btn {
    position: relative;
    border: 1px solid var(--footer-border);
    border-radius: 100%;
    width: 40px;
    height: 40px;
    color: var(--footer-border);
    transition: stroke 0.3s ease-in-out, border 0.3s ease-in-out;

    svg {
      position: absolute;
      left: 50%;
      top: 50%;
      width: 18px;
      height: 18px;
      color: inherit;
      stroke: currentColor;
      object-fit: cover;
      transform: translate(-50%, -50%);
      transition: stroke 0.3s ease-in-out;
    }

    &_active {
      svg {
        stroke: var(--text-white);
        transition: stroke 0.3s ease-in-out;
      }
    }

    &:hover {
      border: 1px solid var(--text-white);
      transition: border 0.3s ease-in-out;

      svg {
        stroke: var(--text-white);
      }
    }
  }

  &__pagination.swiper-pagination {
    visibility: hidden;

    @include small-tablet {
      left: 7% !important;
      top: 270px;
      visibility: visible;
    }
  }

  &__expert {
    width: 50%;
    height: 100%;
  }

  &__cards {
    display: flex;
    width: 100%;
    height: 100%;
  }

  &__prev {
    &::after {}
  }
}

.swiper-pagination-bullet {
  background-color: white;
}

.swiper-pagination.slider-experts__pagination {
  .swiper-pagination-bullet.swiper-pagination-bullet-active-next {
    opacity: 0.7;
    transform: scale(0.8) !important;
  }

  .swiper-pagination-bullet.swiper-pagination-bullet-active-next-next {
    opacity: 0.7;
    transform: scale(0.8) !important;
  }
}