.partnership {
  z-index: 10;
  padding-top: 100px;
  padding-bottom: var(--block-top-desk);
  background-color: var(--secondary-white);

  &__title {
    margin: 0;
    padding-bottom: 40px;
  }

  @include tablet {
    padding-top: 0;
    padding-bottom: var(--block-top-tablet);
  }

  @include small-mobile {
    margin-top: 233px;
    padding-top: 200px;
  }
}

.testimonials {
  display: flex;
  border: 1px solid var(--header-border);

  &__card {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  &__item {
    padding: 32px 24px 24px;
    background-size: 100% 0;
    transition: background-size 0.3s ease-in-out, color 0.5s ease-in-out;
    cursor: pointer;

    .testimonials__svg-path {
      transition: stroke 0.3s ease-in-out;
      stroke: var(--text-color);
    }

    &:not(:last-child) {
      border-right: 1px solid var(--header-border);
    }

    &:hover {
      background: linear-gradient(#8a8cce, #8a8cce);
      background-position: 0% 100%;
      background-size: 100% 100%;
      background-repeat: no-repeat;
      transition: background-size 0.3s ease-in-out, color 0.5s ease-in-out;

      .testimonials__title {
        color: var(--text-white);
        transition: color 0.2s ease-in-out;
      }

      .testimonials__text {
        color: var(--text-white);
        transition: color 0.2s ease-in-out;
      }

      .testimonials__btn {
        color: var(--text-white);
        transition: color 0.2s ease-in-out;
      }

      .testimonials__svg-path {
        stroke: #fff;
        transition: stroke 0.2s ease-in-out;
      }
    }

    @include tablet {
      &:not(:last-child) {
        border-right: none;
      }

      &:not(:first-child) {
        border-top: 1px solid var(--header-border);
      }
    }
  }

  &__text {
    padding-bottom: 10px;

    @include tablet {
      margin: 0;
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 266px;
  }

  &__img {
    align-items: center;
  }

  &__title {
    margin: 0;
    padding-bottom: 15px;
    font-size: 24px;
    transition: color 0.5s ease-in-out;

    @include tablet {
      font-size: 20px;
    }
  }

  &__content {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    height: 100%;
    font-family: "PTRoot", sans-serif;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: -0.32px;
  }

  &__btn {
    // margin-top: auto;
    font-family: "PTRoot", sans-serif;
    font-size: 18px;
    line-height: 90%;
    letter-spacing: -0.36px;
    white-space: nowrap;
    text-transform: lowercase;
  }

  @include tablet {
    flex-direction: column;

  }
}