.form {
  &__input {
    font-family: "DrukCyr", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 48px;
    line-height: 110%;
    letter-spacing: 1.44px;
    text-transform: uppercase;
    color: var(--text-white);
    background-color: var(--text-color);
    transition: all 0.8s;

    &::placeholder {
      color: var(--text-white);

      @include mobile {
        font-size: 28px;
      }
    }

    @include mobile {
      font-size: 28px;
    }
  }

  &__label {
    position: relative;
    margin-bottom: 32px;
    border-bottom: 1px solid var(--footer-border);
    padding: 12px;
    color: var(--text-white);
    transition: all 0.3s;
    animation: 0.8s fadein ease-in-out;
  }

  &__btn {
    &_text {
      display: block;
      width: 100%;
      margin: 0;
      border: 1px solid var(--header-border);
      padding: 20px 32px 20px 20px;
      font-family: "PTRoot", sans-serif;
      font-size: 18px;
      line-height: 90%;
      letter-spacing: -0.36px;
      white-space: nowrap;
      text-align: center;
      text-transform: lowercase;
      color: var(--text-color);
      background-color: var(--light-color);
      transition: background-color 0.3s, color 0.3s;

      &:hover {
        border: 1px solid var(--main-color);
        color: var(--text-white);
        background-color: var(--main-color);
        transition: background-color 0.3s, color 0.3s;
      }
    }
  }

  .just-validate-error-label {
    position: absolute;
    margin-top: 20px;
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 12px;
    line-height: 100%;
    letter-spacing: -0.24px;
    color: var(--error-tomato) !important;
    animation: 0.8s fadein ease-in-out;
  }

  .just-validate-error-field {
    color: var(--error-tomato) !important;

    &::placeholder {
      color: var(--error-tomato) !important;
    }

    @include mobile {
      font-size: 28px;
    }
  }

  &:active {
    background: var(--main-color-active);
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}