.modal {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 55;
  width: 100%;
  height: 100vh;
  background-color: var(--text-color);
  visibility: hidden;
  transform: translateY(-1800px);
  transition: transform 0.5s ease-in-out, visibility 0.5s ease-in-out;
  cursor: pointer;

  &_active {
    visibility: visible;
    transform: translateY(0);
    transition: transform 0.5s ease-in-out, visibility 0.5s ease-in-out;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--footer-border);
  }

  &__bottom {
    display: flex;
    flex-wrap: wrap;
    padding: 0 var(--container-offset);
    padding-bottom: 40px;
    height: 94%;

    @include tablet {
      flex-wrap: nowrap;
      flex-direction: column;
    }

    @include mobile {
      padding: 0 var(--container-offset-low);
    }
  }

  &__logo-link {
    border-right: 1px solid var(--footer-border);
    padding: 24px 40px;

    &:hover path {
      fill: var(--main-color);
      transition: fill 0.2s ease-in-out;
    }

    @include tablet {
      padding: 24px 20px;
    }
  }

  &__logo-svg path {
    fill: var(--text-white);
    transition: fill 0.2s ease-in-out;
  }

  &__burger {
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    background: var(--text-color);
  }

  &__burger-btn {
    position: relative;
    border: none;
    border-left: 1px solid var(--footer-border);
    padding: 0;
    width: 64px;
    height: 64px;
    background: var(--text-color);
    cursor: pointer;
  }

  &__svg {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 16px;
    height: 16px;
    fill: var(--text-white);
    transform: translate(-50%, -50%);
    transition: fill 0.2s ease-in-out;

    &:hover {
      fill: var(--main-color);
      transition: fill 0.2s ease-in-out;
    }
  }

  &__container {
    @include big-desktop {
      max-width: var(--content-width-middle);
    }
  }

  &__contacts {
    flex-grow: 1;
    align-self: flex-end;

    .contacts__item {
      &:not(:last-child) {
        margin-bottom: 16px;
      }

      @include tablet {
        align-self: center;
        padding-right: 40px;

        &:not(:last-child) {
          margin-bottom: 0px;
        }
      }

      @include mobile {
        padding-right: 5px;
      }
    }

    @include tablet {
      display: flex;
      align-self: flex-start;
      order: 2;
    }

    @include mobile {
      display: flex;
      flex-grow: 1;
      align-items: flex-start;
      justify-content: space-between;
      width: 100%;
    }
  }

  &__text-wrap {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__call-form {
    display: flex;
    flex-direction: column;
    align-self: self-end;
    justify-content: flex-end;
    padding-top: 136px;
    width: 50%;

    @include tablet {
      width: 100%;
    }

    @include mobile {
      flex-grow: 1;
      padding-top: 20px;
    }
  }

  &__form {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  &__title {
    margin-top: 40px;
    margin-bottom: 16px;
    text-align: left;
    color: var(--text-white);
  }

  &__text {
    margin-bottom: auto;
    padding-right: 750px;
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: -0.4px;
    text-align: left;
    color: var(--text-white);

    @include tablet {
      padding-right: 200px;
    }

    @include mobile {
      padding-right: 100px;
    }

    @include small-mobile {
      font-size: 16px;
      padding-right: 0;
    }
  }

  &__small {
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 12px;
    line-height: 100%;
    letter-spacing: -0.24px;
    color: var(--text-grey-mini);
  }
}

.modal-done {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 15;
  width: 100%;
  height: 100vh;
  background-color: var(--text-color);
  visibility: hidden;
  transform: translateY(-1800px);
  transition: transform 0.8s ease-in-out, visibility 0.8s ease-in-out;
  cursor: pointer;

  &_active {
    visibility: visible;
    transform: translateY(0);
    transition: transform 0.8s ease-in-out, visibility 0.8s ease-in-out;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--footer-border);
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 94%;
    padding: 0 var(--container-offset);
    padding-bottom: 40px;

    @include mobile {
      padding: 0 var(--container-offset-low);
      justify-content: flex-start;
      height: 100%;
    }
  }

  &__logo-link {
    border-right: 1px solid var(--footer-border);
    padding: 24px 40px;

    @include tablet {
      padding: 24px 0;
    }
  }

  &__logo-svg path {
    fill: var(--text-white);
  }

  &__burger {
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    background: var(--text-color);
  }

  &__burger-btn {
    position: relative;
    border: none;
    border-left: 1px solid var(--footer-border);
    padding: 0;
    width: 64px;
    height: 64px;
    background: var(--text-color);
    cursor: pointer;
  }

  &__svg {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 16px;
    height: 16px;
    fill: var(--text-white);
    transform: translate(-50%, -50%);
    transition: fill 0.3s ease-in-out;

    &:hover {
      fill: var(--main-color);
      transition: fill 0.3s ease-in-out;
    }
  }

  &__phone-svg {
    margin-top: 112px;
    border: 1px solid var(--footer-border);
    border-radius: 100%;
    padding: 34px;
    width: 148px;
    height: 148px;
    fill: var(--main-color);
    background-color: rgba(138, 140, 206, 0.16);
    transition: fill 0.3s ease-in-out;

    &:hover {
      fill: var(--text-white);
      transition: fill 0.3s ease-in-out;
    }

    @include mobile {
      margin-top: 24px;
      width: 96px;
      height: 96px;
    }
  }

  &__container {
    @include big-desktop {
      max-width: var(--container-width);
    }
  }

  &__text-wrap {
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;
    width: 50%;

    @include mobile {
      flex-grow: 1;
      width: 90%;
    }
  }

  &__title {
    margin-top: 40px;
    margin-bottom: 16px;
    text-align: center;
    color: var(--text-white);

    @include mobile {
      font-size: 48px;
    }
  }

  &__text {
    margin-top: 0;
    margin-bottom: 0;
    padding-right: 0;
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: -0.4px;
    text-align: center;
    color: var(--text-white);

    @include mobile {
      padding: 0 40px;
      font-size: 16px;
    }
  }

  &__btn {
    margin-bottom: 72px;

    @include mobile {
      width: 100%;
    }
  }
}