.header {
  position: relative;
  // z-index: 10;

  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;

  &__container {
    margin: 0 auto;

    @include for-full-hd {
      padding: 0 var(--container-offset-full-hd);
    }
  }

  &__controls {
    position: relative;
    z-index: 4;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--header-border);
    width: 100%;
  }

  &__wrapper {
    display: flex;
    flex-grow: 1;
    justify-content: space-between;
    background-color: #fff;
    background: transparent;

    @include tablet {
      position: absolute;
      left: 0;
      top: 66px;
      flex-direction: column;
      justify-content: flex-start;
      overflow: hidden;
      width: 100%;
      height: calc(100vh + 1px);
      transition: all 0.3s ease-in-out;
    }

    @include small-mobile {
      top: 55px;
    }
  }

  &__nav {
    position: relative;
    z-index: 2;
    display: flex;
    flex-grow: 1;
    justify-content: space-around;
    transition: all 0.9s ease-in-out;

    @include tablet {
      flex-grow: 0;
      flex-direction: column;
      justify-content: flex-start;
      background-color: var(--text-color);
      transform: translateY(-1200px);
      transition: all 0.6s ease-in-out;
    }
  }

  &__logo-link {
    border-right: 1px solid var(--header-border);
    padding: 24px 40px;
    cursor: pointer;

    &:hover {
      background-color: var(--main-color);
      transition: background-color 0.3s ease-in-out;
    }

    &:hover path {
      fill: var(--text-white);
      transition: color 0.3s ease-in-out;
    }

    @include tablet {
      padding: 24px 20px;
    }

    @include small-mobile {
      border-right: 0;
      padding: 18px 20px;
    }
  }

  &__logo-svg path {
    fill: #000;
    transition: fill 0.3s ease-in-out;
  }

  &__burger {
    display: flex;
    justify-content: space-between;
    padding: 20px 16px 20px 20px;
    font-family: "PTRoot", sans-serif;
    font-weight: 500;
    font-style: normal;
    background: transparent;

    @include for-desktop {
      display: none;
    }

    @include tablet {
      border: none;
      padding: 20px;
      font-size: 18px;
      line-height: 90%;
      letter-spacing: -0.36px;
      white-space: nowrap;
      text-transform: lowercase;
    }

    @include small-tablet {
      justify-content: center;
      padding-left: 0;
    }

    @include mobile {
      justify-content: space-around;
      padding-left: 16px;
      font-size: 14px;
      line-height: 100%;
      letter-spacing: -0.7px;
      text-align: center;
      text-transform: lowercase;
      visibility: visible;
    }

    @include small-mobile {
      border-left: 1px solid var(--header-border);
    }
  }

  &__burger-text {
    align-self: center;
    font-size: 14px;
    line-height: 100%;
    letter-spacing: -0.7px;
    text-align: center;
    text-transform: lowercase;
  }

  &__btn-call.btn-white {
    border: none;
    border-left: 1px solid var(--header-border);

    @include tablet {
      z-index: 2;
      visibility: hidden;
    }
  }

  &__img {
    z-index: 2;
    position: relative;
    width: 100%;
    height: 1336px;

    @include small-mobile {
      height: 1336px;
    }
  }

  &__text {
    position: fixed;
    z-index: 3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 80px;
    padding: 0 167px;
    padding-top: 40px;

    &_under {
      align-self: center;
      margin: 0 auto;
      max-width: 490px;
      font: 500 20px "PTRoot", sans-serif;
      font-style: normal;
      line-height: 140%;
      letter-spacing: -0.4px;
      text-align: center;
      color: #fff;

      @include small-mobile {
        font-size: 16px;
      }
    }

    @include small-tablet {
      padding: 40px 110px 0;
    }

    @include small-mobile {
      padding: 40px 16px 0;
      font-size: 16px;
    }
  }

  &__title {
    z-index: 1;
    align-self: center;
    margin: 0 auto;
    padding-bottom: 32px;
    font-weight: 500;
    font-style: normal;
    font-size: 96px;
    line-height: 100%;
    letter-spacing: 0.96px;
    text-align: center;
    text-transform: uppercase;
    color: #fff;

    @include for-full-hd {
      padding: 40px var(--container-offset-full-hd);
    }

    @include tablet {
      font-size: 69px;
    }

    @include small-tablet {
      font-size: 54px;
    }

    @include small-mobile {
      font-size: 48px;
      line-height: 48px;
      letter-spacing: 1.44px;
    }
  }
}

.header__fixed {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 25;
  background-color: #fff;
  transform: translateY(-150px);
  transition: transform 0.4s ease-in-out;

  &.low-move {
    transform: translateY(0);
    transition: transform 0.4s ease-in-out;
  }
}
